* {
  box-sizing: border-box;
  scrollbar-color: thistle lavenderblush;
  scrollbar-width: thin;
}
body {
  margin: 4rem 0 0 0;
  padding-bottom: 5rem;
  background-color: lavenderblush;
  background-image: url('media/goodbye1.png');
  background-repeat: no-repeat;
  background-position: center;
  font-family: 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell',
    'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
}

.flex-container {
  display: flex;
  justify-content: center;
}

ul {
  margin-top: 1rem;
  list-style-type: decimal-leading-zero;
}

li {
  margin: 1rem auto 1rem auto;
  font-weight: bold;
}

h1 {
  margin-top: 0;
  padding: 1rem;
  text-align: center;
  text-decoration: line-through aquamarine;
  background-color: lavender;
  opacity: 95%;
}
h2 {
  margin-top: 1rem;
}
.list {
  width: 25%;
  height: 26rem;
  overflow: hidden;
  overflow-y: scroll;
  padding: 1rem;
  border-radius: 0.5rem;
  background-color: aquamarine;
  opacity: 95%;
}
.form {
  width: 25%;
  padding: 1rem;
  border-radius: 0 0.5rem 0.5rem 0;
  background-color: lavender;
  opacity: 95%;
}

.inputs {
  text-align: center;
}

input {
  width: 85%;
  height: 3rem;
  padding: 0.5rem;
  border-radius: 0.5rem;
  margin-bottom: 1rem;
}
@media only screen and (max-width: 600px) {
  /* MOBILE */
  * {
    scrollbar-color: thistle aquamarine;
  }
  body {
    margin: 0;
    padding-bottom: 1rem;
    background-size: 85%;
    background-position-x: center;
    background-position-y: 25%;
  }
  h1 {
    text-align: left;
  }
  .flex-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-left: auto;
    margin-right: auto;
  }
  .list {
    width: 95%;
    border-radius: 0;
  }
  .form {
    width: 95%;
    border-radius: 0;
  }
}
